import React, { useRef, useEffect, useCallback, forwardRef, useImperativeHandle, useState, memo, useMemo } from 'react';
import { SECTIONS } from '../constants';
import Lottie from 'lottie-react';
import simoAnimation from '../assets/simo.json';
import identityDesignAnimation from '../assets/identity_design.json';
import digitalExperiencesAnimation from '../assets/digital_experiences.json';
import connectionAnimation from '../assets/connection.json';
import Tags from './Tags';

// Move debounce function outside component to prevent recreation
const debounce = (func, wait) => {
  let timeout;
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout);
      func(...args);
    };
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
  };
};

// Memoized Tags component to prevent unnecessary re-renders
const MemoizedTags = memo(Tags);

const MainContent = forwardRef(({ setActiveLink }, ref) => {
  // Create refs for each section first
  const section1Ref = useRef(null);
  const section2Ref = useRef(null);
  const section3Ref = useRef(null);
  const section4Ref = useRef(null);

  // Memoize the array of refs
  const sectionRefs = useMemo(() => [
    section1Ref,
    section2Ref,
    section3Ref,
    section4Ref
  ], []); // Empty dependency array since refs don't change
  
  // Add a ref to track initial load
  const isInitialLoad = useRef(true);
  
  // Add refs for the Lottie animations
  const simoLottieRef = useRef(null);
  const identityDesignLottieRef = useRef(null);
  const digitalExperiencesLottieRef = useRef(null);
  const connectionLottieRef = useRef(null);
  
  // Track active section
  const [activeSection, setActiveSection] = useState("section1");
  
  // Track previous active section to optimize animation control
  const prevActiveSectionRef = useRef("section1");
  
  // Track animation completion state - once true, stays true
  const [animationsComplete, setAnimationsComplete] = useState({
    section1: false,
    section2: false,
    section3: false,
    section4: false
  });

  // Define tags for each section
  const sectionTags = {
    section1: ["simo duric", "graphic design", "ui design", "phygrid", "uae"],
    section2: ["logo design", "corporate identity", "branding", "visual direction", "for sale"],
    section3: ["ui design", "user experience", "design systems", "product design"],
    section4: ["email", "instagram", "layers"]
  };

  // Handle animation complete events - once complete, stays complete
  const handleAnimationComplete = useCallback((sectionId) => {
    setAnimationsComplete(prev => ({
      ...prev,
      [sectionId]: true
    }));
  }, []);

  // Expose the resetAnimation method to the parent component
  useImperativeHandle(ref, () => ({
    resetAnimation: (sectionId) => {
      // Reset the animation based on the section
      if (sectionId === "section1" && simoLottieRef.current) {
        simoLottieRef.current.goToAndPlay(0, true);
      } else if (sectionId === "section2" && identityDesignLottieRef.current) {
        identityDesignLottieRef.current.goToAndPlay(0, true);
      } else if (sectionId === "section3" && digitalExperiencesLottieRef.current) {
        digitalExperiencesLottieRef.current.goToAndPlay(0, true);
      } else if (sectionId === "section4" && connectionLottieRef.current) {
        connectionLottieRef.current.goToAndPlay(0, true);
      }
      
      // Reset completion state for the section
      setAnimationsComplete(prev => ({
        ...prev,
        [sectionId]: false
      }));
    }
  }));

  // Optimized animation control - only update when active section changes
  useEffect(() => {
    // Skip if the active section hasn't changed
    if (prevActiveSectionRef.current === activeSection) return;
    
    // Update previous active section
    prevActiveSectionRef.current = activeSection;
    
    // Pause all animations except the first one (logo)
    if (identityDesignLottieRef.current) {
      if (activeSection === "section2") {
        identityDesignLottieRef.current.play();
      } else {
        identityDesignLottieRef.current.pause();
      }
    }
    
    if (digitalExperiencesLottieRef.current) {
      if (activeSection === "section3") {
        digitalExperiencesLottieRef.current.play();
      } else {
        digitalExperiencesLottieRef.current.pause();
      }
    }
    
    if (connectionLottieRef.current) {
      if (activeSection === "section4") {
        connectionLottieRef.current.play();
      } else {
        connectionLottieRef.current.pause();
      }
    }
  }, [activeSection]);

  // Optimized scroll handler with increased debounce time
  const handleScroll = useCallback(() => {
    const y = window.scrollY + window.innerHeight / 2;
    
    let newActiveSection = "section1";
    sectionRefs.forEach((ref, index) => {
      const element = ref.current;
      if (!element) return;
      
      const { offsetTop, offsetHeight } = element;
      if (y >= offsetTop && y <= offsetTop + offsetHeight) {
        newActiveSection = SECTIONS[index].id;
      }
    });

    // Only update if the active section has changed
    if (newActiveSection !== activeSection) {
      setActiveSection(newActiveSection);
      setActiveLink(newActiveSection);
      
      const section = SECTIONS.find(s => s.id === newActiveSection);
      const newPath = section ? section.path : "";
      window.history.replaceState(null, '', newPath ? `/${newPath}` : '/');
    }
  }, [sectionRefs, setActiveLink, activeSection]);

  // Separate handler for separator animation
  const handleSeparatorAnimation = useCallback(() => {
    sectionRefs.forEach((ref, index) => {
      if (index === SECTIONS.length - 1) return; // Skip last section
      
      const element = ref.current;
      if (!element) return;

      const separator = element.querySelector('.section-separator');
      if (!separator) return;

      const nextSection = sectionRefs[index + 1]?.current;
      if (!nextSection) return;

      const nextSectionTop = nextSection.offsetTop;
      const viewportHeight = window.innerHeight;
      const scrollPosition = window.scrollY;

      // Calculate how far the next section is from entering the viewport
      const distanceToNextSection = nextSectionTop - scrollPosition;
      
      // Calculate the width ratio (1 when section is at top, 0 when next section is at top)
      const widthRatio = Math.max(0, Math.min(1, distanceToNextSection / viewportHeight));
      
      // Calculate opacity (inverse of width ratio: 0.2 when full width, 1.0 when width is 0)
      const opacity = 0.2 + (1 - widthRatio) * 0.8;
      
      // Apply the width and opacity based on the ratio
      const maxWidth = window.innerWidth <= 768 ? 400 : 500;
      separator.style.width = `${widthRatio * maxWidth}px`;
      separator.style.opacity = opacity.toString();
    });
  }, [sectionRefs]);

  // Create a stable debounced scroll handler for section detection
  const debouncedScroll = useCallback(
    debounce(handleScroll, 150),
    [handleScroll]
  );

  // Set up scroll event listeners
  useEffect(() => {
    let animationFrameId;
    
    const onScroll = () => {
      // Cancel any pending animation frame
      if (animationFrameId) {
        cancelAnimationFrame(animationFrameId);
      }
      
      // Schedule the separator animation for the next frame
      animationFrameId = requestAnimationFrame(handleSeparatorAnimation);
      
      // Call the debounced handler for section detection
      debouncedScroll();
    };

    window.addEventListener('scroll', onScroll);
    return () => {
      window.removeEventListener('scroll', onScroll);
      if (animationFrameId) {
        cancelAnimationFrame(animationFrameId);
      }
    };
  }, [debouncedScroll, handleSeparatorAnimation]);

  // Initial scroll check
  useEffect(() => {
    if (isInitialLoad.current) {
      // Get the current path from URL
      const path = window.location.pathname.slice(1); // Remove leading slash
      
      // Find the section that matches the path
      const targetSection = SECTIONS.find(section => section.path === path);
      
      if (targetSection) {
        // Find the corresponding ref
        const sectionIndex = SECTIONS.findIndex(s => s.id === targetSection.id);
        const targetRef = sectionRefs[sectionIndex];
        
        if (targetRef.current) {
          // Scroll to the section
          targetRef.current.scrollIntoView({ behavior: 'smooth' });
          // Update active section
          setActiveSection(targetSection.id);
          setActiveLink(targetSection.id);
        }
      } else {
        // If no matching section found, use default scroll behavior
        handleScroll();
      }
      
      // Initialize separator widths
      handleSeparatorAnimation();
      
      isInitialLoad.current = false;
    }
  }, [handleScroll, sectionRefs, setActiveLink, handleSeparatorAnimation]);

  // Function to scroll to section
  const scrollToSection = useCallback((sectionId) => {
    const sectionIndex = SECTIONS.findIndex(s => s.id === sectionId);
    if (sectionIndex >= 0 && sectionRefs[sectionIndex].current) {
      sectionRefs[sectionIndex].current.scrollIntoView({ behavior: 'instant' });
    }
  }, [sectionRefs]);

  // Handle initial route
  useEffect(() => {
    if (isInitialLoad.current) {
      const path = window.location.pathname.slice(1); // Remove leading slash
      const section = SECTIONS.find(s => s.path === path);
      if (section) {
        scrollToSection(section.id);
        setActiveSection(section.id);
        setActiveLink(section.id);
      }
      isInitialLoad.current = false;
    }
  }, [scrollToSection, setActiveLink]);

  return (
    <div className="main-content">
      {SECTIONS.map((section, index) => (
        <div 
          key={section.id} 
          ref={sectionRefs[index]} 
          id={section.id} 
          className="section"
          style={{
            backgroundColor: 'transparent'
          }}
        >
          <div className="centered-container">
            {index === 0 && (
              <>
                <div className="lottie-container">
                  <Lottie 
                    lottieRef={simoLottieRef}
                    animationData={simoAnimation} 
                    loop={false}
                    onComplete={() => handleAnimationComplete("section1")}
                  />
                </div>
                <MemoizedTags 
                  tags={sectionTags.section1} 
                  delay={0}
                  isVisible={animationsComplete.section1}
                />
              </>
            )}
            {index === 1 && (
              <>
                <div className="lottie-container">
                  <Lottie 
                    lottieRef={identityDesignLottieRef}
                    animationData={identityDesignAnimation} 
                    loop={false}
                    autoplay={false}
                    onComplete={() => handleAnimationComplete("section2")}
                  />
                </div>
                <MemoizedTags 
                  tags={sectionTags.section2} 
                  delay={0}
                  isVisible={animationsComplete.section2}
                />
              </>
            )}
            {index === 2 && (
              <>
                <div className="lottie-container">
                  <Lottie 
                    lottieRef={digitalExperiencesLottieRef}
                    animationData={digitalExperiencesAnimation} 
                    loop={false}
                    autoplay={false}
                    onComplete={() => handleAnimationComplete("section3")}
                  />
                </div>
                <MemoizedTags 
                  tags={sectionTags.section3} 
                  delay={0}
                  isVisible={animationsComplete.section3}
                />
              </>
            )}
            {index === 3 && (
              <>
                <div className="lottie-container">
                  <Lottie 
                    lottieRef={connectionLottieRef}
                    animationData={connectionAnimation}
                    loop={false}
                    autoplay={false}
                    onComplete={() => handleAnimationComplete("section4")}
                  />
                </div>
                <MemoizedTags 
                  tags={sectionTags.section4} 
                  delay={0}
                  isVisible={animationsComplete.section4}
                />
              </>
            )}
          </div>
          {/* Add separator for all sections except the last one */}
          {index < SECTIONS.length - 1 && <div className="section-separator" />}
        </div>
      ))}
    </div>
  );
});

export default MainContent;