import React, { useState, useRef } from 'react';
import Sidebar from './components/Sidebar';
import MainContent from './components/MainContent';
import HamburgerButton from './components/HamburgerButton';
import './App.css';

function App() {
  // Get initial section from URL or default to section1
  const initialSection = window.location.pathname.split('/')[1] || "section1";
  const [activeLink, setActiveLink] = useState(initialSection);
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const mainContentRef = useRef(null);

  const handleLogoClick = (activeSection) => {
    // Pass the active section to the MainContent component
    if (mainContentRef.current && mainContentRef.current.resetAnimation) {
      mainContentRef.current.resetAnimation(activeSection);
    }
  };

  return (
    <div className="App">
      <HamburgerButton isOpen={isSidebarOpen} setOpen={setSidebarOpen} />
      <Sidebar 
        isOpen={isSidebarOpen} 
        activeLink={activeLink} 
        setActiveLink={setActiveLink} 
        onLogoClick={handleLogoClick}
      />
      <div className={`overlay ${isSidebarOpen ? 'active' : ''}`} onClick={() => setSidebarOpen(false)} />
      <MainContent ref={mainContentRef} setActiveLink={setActiveLink} />
    </div>
  );
}

export default App;