import React, { useState, useEffect } from 'react';

function Logo({ onClick }) {
  const [isHovered, setIsHovered] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const [showAnimated, setShowAnimated] = useState(false);

  // Handle the fade transition with a slight delay
  useEffect(() => {
    if (isHovered) {
      setShowAnimated(true);
    } else {
      // Add a small delay before hiding the animated logo to allow for fade out
      const timer = setTimeout(() => {
        setShowAnimated(false);
      }, 300); // Match this with the CSS transition duration
      
      return () => clearTimeout(timer);
    }
  }, [isHovered]);

  const handleClick = () => {
    setIsClicked(true);
    if (onClick) onClick();
    
    // Reset the clicked state after a short delay
    setTimeout(() => {
      setIsClicked(false);
    }, 200);
  };

  return (
    <div 
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={handleClick}
      style={{ 
        cursor: 'pointer',
        display: 'inline-block',
        height: '64px',
        width: '48px',
        lineHeight: 0,
        transform: isClicked ? 'scale(0.8)' : 'scale(1)',
        opacity: isClicked ? 0.8 : 1,
        transition: 'transform 0.1s ease, opacity 0.1s ease',
        position: 'relative'
      }}
    >
      {/* Static Logo */}
      <div style={{
        position: 'absolute',
        top: 0,
        left: 0,
        opacity: isHovered ? 0 : 1,
        transition: 'opacity 0.3s ease',
        pointerEvents: 'none'
      }}>
        <svg 
          width="48" 
          height="64" 
          viewBox="0 0 48 64" 
          fill="none" 
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M0 16H48" stroke="white" strokeWidth="2"/>
          <path d="M0 47H48" stroke="white" strokeWidth="2"/>
          <path d="M25 0V64" stroke="white" strokeWidth="2"/>
          <circle cx="24.5" cy="31.5" r="15.5" stroke="white" strokeWidth="2"/>
        </svg>
      </div>
      
      {/* Animated Logo */}
      {showAnimated && (
        <div style={{
          position: 'absolute',
          top: 0,
          left: 0,
          opacity: isHovered ? 1 : 0,
          transition: 'opacity 0.3s ease',
          pointerEvents: 'none'
        }}>
          <svg xmlns="http://www.w3.org/2000/svg" width="48" height="64" viewBox="0 0 48 64" fill="none">
            <style>
              {`@keyframes a0_d { 0% { d: path('M0,16L48,16'); animation-timing-function: cubic-bezier(0.42,0,1,1); } 4% { d: path('M24.9931,15.9405L33.0337,11.0206'); } 8.67% { d: path('M24.9931,15.9405L33.1364,12.199'); } 100% { d: path('M24.9931,15.9405L33.1364,12.199'); } }
              @keyframes a1_d { 0% { d: path('M0,47L48,47'); animation-timing-function: cubic-bezier(0.42,0,1,1); } 4% { d: path('M24.805,47L24.805,47'); } 100% { d: path('M24.805,47L24.805,47'); } }
              @keyframes a2_d { 0% { d: path('M25,0L25,64'); animation-timing-function: cubic-bezier(0.42,0,1,1); } 4% { d: path('M24.7682,14.8722L29.9791,23.5876'); } 8.67% { d: path('M24.7682,14.8722L28.897,24.052'); } 100% { d: path('M24.7682,14.8722L28.897,24.052'); } }
              @keyframes a3_t { 0% { transform: translate(24.5px,31.5px) rotate(0deg); animation-timing-function: cubic-bezier(0.42,0,1,1); } 4% { transform: translate(24.5px,31.5px) rotate(-87deg); } 100% { transform: translate(24.5px,31.5px) rotate(-87deg); } }
              @keyframes a3_da { 0% { stroke-dasharray: 128px; animation-timing-function: cubic-bezier(0.42,0,1,1); } 4% { stroke-dasharray: 90px; } 100% { stroke-dasharray: 90px; } }`}
            </style>
            <path d="M0,16L48,16" stroke="white" strokeWidth="2" transform="translate(24,16) translate(-24,-16)" style={{animation: '10s linear both a0_d'}}/>
            <path d="M0,47L48,47" stroke="white" strokeWidth="2" transform="translate(24,47) translate(-24,-47)" style={{animation: '10s linear both a1_d'}}/>
            <path d="M25,0L25,64" stroke="white" strokeWidth="2" transform="translate(25,32) translate(-25,-32)" style={{animation: '10s linear both a2_d'}}/>
            <path stroke="white" strokeWidth="2" d="M15.5,0C15.5,8.56041,8.56041,15.5,0,15.5C-8.56041,15.5,-15.5,8.56041,-15.5,0C-15.5,-7.32771,-10.4151,-13.4678,-3.58139,-15.0842C-2.43179,-15.3561,-1.2327,-15.5,0,-15.5C8.56041,-15.5,15.5,-8.56041,15.5,0Z" strokeDasharray="128" transform="translate(24.5,31.5)" style={{animation: '10s linear both a3_t, 10s linear both a3_da'}}/>
          </svg>
        </div>
      )}
    </div>
  );
}

export default Logo;