import React, { useEffect, useRef, useCallback } from 'react';
import { Link } from 'react-scroll';
import Logo from './Logo';

function Sidebar({ isOpen, activeLink, setActiveLink, onLogoClick }) {
  const paddingValues = {
    "section1": "128px",
    "section2": "96px",
    "section3": "64px",
    "section4": "32px"
  };

  const padding = paddingValues[activeLink];
  const linkRefs = useRef({});

  // Function to update link styles based on scroll position
  const updateLinkStyles = useCallback(() => {
    const viewportHeight = window.innerHeight;
    const scrollPosition = window.scrollY;
    
    // Get all section elements
    const sections = ["section1", "section2", "section3", "section4"].map(id => 
      document.getElementById(id)
    ).filter(Boolean);
    
    // For each section, calculate its position relative to viewport
    sections.forEach((section, index) => {
      const sectionId = `section${index + 1}`;
      const linkRef = linkRefs.current[sectionId];
      if (!linkRef) return;
      
      // Find the actual DOM element using the ref
      const linkElement = linkRef.querySelector('a');
      if (!linkElement) return;
      
      const { offsetTop, offsetHeight } = section;
      const distanceFromTop = offsetTop - scrollPosition;
      
      // Calculate how far the section is from the center of the viewport
      const distanceFromCenter = Math.abs(distanceFromTop + offsetHeight/2 - viewportHeight/2);
      const maxDistance = viewportHeight;
      
      // Calculate the transition ratio (1 when section is centered, 0 when far from center)
      const ratio = Math.max(0, Math.min(1, 1 - distanceFromCenter / maxDistance));
      
      // Apply styles based on the ratio
      linkElement.style.opacity = (0.8 + ratio * 0.2).toString();
      linkElement.style.fontWeight = Math.round(400 + ratio * 300).toString();
      
      // Update active link based on which section is most visible
      if (ratio > 0.5 && setActiveLink) {
        setActiveLink(sectionId);
      }
    });
  }, [setActiveLink]);

  // Set up scroll event listener
  useEffect(() => {
    const handleScroll = () => {
      requestAnimationFrame(updateLinkStyles);
    };
    
    window.addEventListener('scroll', handleScroll);
    // Initial update
    updateLinkStyles();
    
    return () => window.removeEventListener('scroll', handleScroll);
  }, [updateLinkStyles]);

  return (
    <div className={`sidebar ${isOpen ? 'open' : ''}`}>
      <div className="menu">
        <div className="logoname">
          <Logo onClick={() => onLogoClick && onLogoClick(activeLink)} />
          <div className="name">finding</div>
        </div>
        <div className="sidebarlinks" style={{ paddingTop: padding, transition: 'padding-top 0.1s ease-in-out' }}>
          <div ref={el => linkRefs.current.section1 = el}>
            <Link 
              className={activeLink === "section1" ? "active" : ""} 
              to="section1" 
              smooth={true} 
              duration={300}
            >
              simo
            </Link>
          </div>
          <div ref={el => linkRefs.current.section2 = el}>
            <Link 
              className={activeLink === "section2" ? "active" : ""} 
              to="section2" 
              smooth={true} 
              duration={300}
            >
              identity design
            </Link>
          </div>
          <div ref={el => linkRefs.current.section3 = el}>
            <Link 
              className={activeLink === "section3" ? "active" : ""} 
              to="section3" 
              smooth={true} 
              duration={300}
            >
              digital experiences
            </Link>
          </div>
          <div ref={el => linkRefs.current.section4 = el}>
            <Link 
              className={activeLink === "section4" ? "active" : ""} 
              to="section4" 
              smooth={true} 
              duration={300}
            >
              connection
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Sidebar;