import React from 'react';
import './HamburgerButton.css';

function HamburgerButton({ isOpen, setOpen }) {
  return (
    <button className={`hamburger-button ${isOpen ? 'open' : ''}`} onClick={() => setOpen(!isOpen)}>
      <span></span>
      <span></span>
      <span></span>
    </button>
  );
}

export default HamburgerButton;