import React, { useState, useEffect } from 'react';
import { 
  EnvelopeSimple, 
  InstagramLogo, 
  Stack,
  // Add more icons as needed
} from "@phosphor-icons/react";

// Map of tag text to icon components
const tagIcons = {
  "email": EnvelopeSimple,
  "instagram": InstagramLogo,
  "layers": Stack,
  // Add more mappings as needed
};

// Map of tag text to tag type
const tagTypes = {
  "phygrid": "link",
  "email": "link",
  "instagram": "link",
  "layers": "link",
  "for sale": "warning",
  // Add more mappings as needed
};

// Map of tag text to link URLs
const tagLinks = {
  "phygrid": "https://phygrid.com",
  "email": "mailto:hello@findingsimo.com",
  "instagram": "https://instagram.com/findingsimocom",
  "layers": "https://layers.to/findingsimo",
  // Add more mappings as needed
};

function Tags({ tags, isVisible = true }) {
  const [shouldShow, setShouldShow] = useState(false);

  useEffect(() => {
    // Update visibility based on the isVisible prop
    setShouldShow(isVisible);
  }, [isVisible]);

  return (
    <div className={`tags-container ${shouldShow ? 'visible' : ''}`}>
      {tags.map((tag, index) => {
        // Check if there's an icon for this tag
        const IconComponent = tagIcons[tag];
        
        // Check if this tag has a special type
        const tagType = tagTypes[tag] || "default";
        
        // Check if this tag has a link
        const tagLink = tagLinks[tag];
        
        // Determine the tag class based on type
        const tagClass = `tag tag-${tagType}`;
        
        // Create the tag content with icon if available
        const tagContent = IconComponent ? (
          <>
            <IconComponent size={16} weight="regular" className="tag-icon" />
            {tag}
          </>
        ) : tag;
        
        // Render the tag with appropriate wrapper based on type
        if (tagType === "link" && tagLink) {
          return (
            <a 
              key={index} 
              href={tagLink} 
              className={tagClass}
              target="_blank"
              rel="noopener noreferrer"
            >
              {tagContent}
            </a>
          );
        } else {
          return (
            <span key={index} className={tagClass}>
              {tagContent}
            </span>
          );
        }
      })}
    </div>
  );
}

export default Tags; 